import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Recommend from '../views/Recommend.vue'
import HotSongList from '../views/HotSongList.vue'
import Search from '../views/Search.vue'
import SongListDetail from '../views/SongListDetail.vue'
import HotSuggest from '../components/HotSuggest.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'home',
    component: Home,
    redirect:'/recommend',
    children: [{
        path: 'recommend',
        component: Recommend,
      },
      {
        path: 'hot',
        component: HotSongList,
      },
      {
        path: 'search',
        component: Search,
        
      }
    ]
  },
  {
    path:'/songlistdetail',
    name:'songlistdetail',
    component:SongListDetail
  }

]

const router = new VueRouter({
  routes
})

export default router