<template>
  <div class="hot-song-rank" >
    <div class="hot-song-rank" v-if="hotSongRank">
    <header :style="{backgroundImage:'url('+hotSongRank.coverImgUrl+')'}"></header>
    <ul>
      <SongItem
        v-for="(item, index) in hotSongRank.tracks"
        :key="index"
        :songitem="item"
        :index="index+1"
        :isSongList="true"
        :playList="hotSongRank.tracks"
        @translate-song-id="$emit('translate-song-id',$event)"
      ></SongItem>
    </ul>
    </div>
    <div class="roading-page" v-if="isRoading">
      <Roading></Roading>
    </div>
  </div>
</template>

<script>
import SongItem from "@/components/SongItem.vue";
import Roading from "@/components/Roading.vue";
export default {
  components: {
    SongItem,
    Roading
  },
  data() {
    return {
      hotSongRank: null,
      isRoading: false
    };
  },
  created() {
    this.isRoading = true;
    this.axios
      .get("https://music.kele8.cn/top/list?idx=1")
      .then(response => {
        // window.console.log(response.data.playlist)
        // 解构赋值
        ({ playlist: this.hotSongRank } = response.data);
      })
      .finally(() => {
        this.isRoading = false;
      });
  }
};
</script>

<style lang="less">
.hot-song-rank {
  header {
    width: 100vw;
    height: 140px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  ul {
    li {
      &:nth-child(-n + 3) {
        .index {
          color: red;
        }
      }
    }
  }
  .roading-page {
    .roading{
      top:30%;
    }
  }
}
</style>