<template>
  <div class="song-list-detail" >
    <div class="song-list-detail" v-if="detail">
    <div class="detail-head">
      <SongListInfo :detail="detail"></SongListInfo>
      <div class="tags" v-if="!detail2">
        <p>
          标签：
          <span v-for="(item, index) in detail.tags" :key="index">{{item}}</span>
        </p>
        <p>
          简介：
          <span class="des">{{detail.description}}</span>
        </p>
      </div>
      <div class="go-back" @click="back">
        <i class="fa fa-chevron-left" aria-hidden="true"></i>
      </div>
    </div>
    <p class="list">歌曲列表</p>
    <ul class="song-list">
      <SongItem
        v-for="(item, index) in detail1"
        :key="index"
        :songitem="item"
        :index="index+1"
        :isSongList="true"
        :playList="detail1"
        @translate-song-id="$emit('translate-song-id',$event)"
      ></SongItem>
    </ul>
    </div>
    <div class="roading-page" v-if="isRoading">
      <Roading></Roading>
    </div>
    
  </div>
</template>

<script>
import SongListInfo from "@/components/SongListInfo.vue";
import SongItem from "@/components/SongItem.vue";
import Roading from "@/components/Roading.vue";
export default {
  components: {
    SongListInfo,
    SongItem,
    Roading
  },
  data() {
    return {
      detail:null,
      detail1: null,
      detail2: null,
      isRoading:false
    };
  },

  beforeRouteEnter(to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    next(vm => {
      vm.getSongListDetail(vm.$route.query.id);
    });
  },
  methods: {
    getSongListDetail: function(id) {
      this.isRoading=true
      this.axios
        .get(`http://musicapi.leanapp.cn/playlist/detail?id=${id}`)
        .then(response => {
          // window.console.log(response.data);
          this.detail = response.data.playlist;
          this.detail1 = response.data.playlist.tracks;
          this.detail2 = response.data.playlist.updateFrequency;
        }).finally(() => {
        this.isRoading = false;
      });
    },
    back: function() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less">
.song-list-detail {
  .detail-head {
    position: relative;
    .tags {
      padding: 10px 15px 15px;
      text-align: left;
      color: rgb(95, 95, 95);
      p {
        margin-bottom: 10px;
        span {
          border: 1px solid rgb(228, 227, 227);
          border-radius: 10px;
          font-size: 14px;
          margin-right: 10px;
          padding: 1px 2px;
        }
        span.des {
          border: none;
          line-height: 1.5em;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
      }
    }
    .go-back {
      color: white;
      position: absolute;
      top: 5px;
      left: 0px;
      text-align: center;
      background: rgba(179, 179, 179, 0.212);
      z-index: 2;
      width: 36px;
      height: 30px;
      border-radius: 0 30px 30px 0;
      padding-right: 5px;
      line-height: 30px;
      i {
        font-size: 18px;
        font-weight: 300;
      }
      // background: red;
    }
  }

  .list {
    padding: 5px 0;
    font-size: 12px;
    background: rgba(153, 153, 153, 0.274);
    color: rgb(75, 75, 75);
    text-align: left;
    padding-left: 10px;
  }
  .roading-page{
    width:100vw;
    height:100vh;
    background: #fff;
    position: absolute;
    top:0;
    left: 0;
  }
}
</style>