<template>
  <div class="info">
    <div
      class="buy"
      v-if="detail.updateFrequency"
      :style="{backgroundImage:'url('+detail.backgroundCoverUrl+')'}"
    >
      <h5>{{detail.name}}</h5>
      <span>{{detail.updateFrequency}}</span>
      <p>{{detail.description}}</p>
    </div>
    <div class="free" v-else-if="!detail.updateFrequency" >
      <div class="bg" :style="{backgroundImage:'url('+detail.coverImgUrl+')'}"></div>
      <div class="card">
        <div class="cover-img">
          <img :src="detail.coverImgUrl" alt="">
          <span class="sl">歌单</span>
          <span class="play-count"><i class="fa fa-headphones" aria-hidden="true"></i>{{translatePlayNum(detail.playCount)}}</span>
        </div>
        <div class="info-title">
          <h2>{{detail.name}}</h2>
          <a href="">
            <img :src="detail.creator.avatarUrl" alt="">
            {{detail.creator.nickname}}
          </a>
        </div>
        </div> 
        


    </div>
  </div>
</template>

<script>
export default {
  name: "SongListInfo",
  props: {
    detail: Object
  },
  methods: {
    translatePlayNum: function(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      }
      if (n > 99999) {
        return (n / 10000).toFixed(1) + "万";
      }
    },
  },
};
</script>

<style lang="less">
.info {
  .buy {
    background: url();
    text-align: left;
    padding: 40px 20px 30px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: white;
    
    filter: brightness(0.9);
    h5 {
      
    }
    span {
      font-size: 12px;
      background: #999;
      color: rgb(233, 233, 233);
      display: inline-block;
      margin: 15px 0;
    }
    p {
      font-size: 14px;
    }
  }
  .free {
     padding: 40px 20px 30px;
     color:white;
     position: relative;
     overflow: hidden;
     .bg{
       position: absolute;
       top:0;
       left:0;
       width:100%;
       height:100%;
       filter: blur(20px) brightness(0.8);
       transform: scale(1.5);
        background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
     }
    .card{
      position: relative;
       top:0;
       left:0;
      display: flex;
      .cover-img{
        width:130px;
        height:130px;
        position: relative;
        span{
          position: absolute;
          font-size: 12px;
        }
        span.sl{
          left:0;
          top:10px;
          background: rgba(255, 0, 0, 0.795);
          border-radius: 0 10px 10px 0;
          padding:1px 3px;
        }
        span.play-count{
          right:5px;
          top:5px;
        }
      }
      .info-title{
        height:130px;
        flex: 1;
        padding-left: 10px;
        font-size: 16px;
        text-align: left;
        a{
          margin-top:15px;
          text-decoration: none;
          color:rgb(223, 223, 223);
          display: inline-block;
          line-height: 30px;
          font-size: 14px;
          
          img{
            vertical-align: middle;
          width:30px;
          height:30px;
          border-radius: 50%;
        }
        }
        
      }
    }

  }
}
</style>