<template>
  <li @click="palySong(songitem.id)">
    <div class="song" v-if="isSongList">
      <div class="rank">
      <span class="index">{{index>9?index:('0'+index)}}</span>
      </div>
      <div class="name">
        <p>{{songitem.name}}</p>
        <p class="author">{{songitem.ar[0].name}}-{{songitem.al.name}}</p>
      </div>
      <div class="play">
        <span>
          <i class="fa fa-play" aria-hidden="true"></i>
        </span>
      </div>
    </div>

    <div class="song" v-else-if="isNewSong">
      <div class="name new-song">
        <p>{{songitem.name}}</p>
        <p class="author">
          <span class="sq"></span>
          {{songitem.song.artists[0].name}}-{{songitem.song.album.name}}
        </p>
      </div>
       <div class="play">
        <span>
          <i class="fa fa-play" aria-hidden="true"></i>
        </span>
      </div>
    </div>
    <div class="song" v-else-if="isSearchSong">
      <div class="name new-song">
        <p>{{songitem.name}}</p>
        <p class="author">
          <span class="sq"></span>
          {{songitem.artists[0].name}}-{{songitem.album.name}}
        </p>
      </div>
       <div class="play">
        <span>
          <i class="fa fa-play" aria-hidden="true"></i>
        </span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  name: "SongItem",
  props: {
    songitem: Object,
    index: Number,
    isNewSong: Boolean,
    isSongList: Boolean,
    isSearchSong: Boolean,
    playList:Array
  },
  methods: {
    palySong: function(id) {
      // window.console.log(this.playList);
      this.$emit("translate-song-id", {
        id:id,
        playList:this.playList,
        currentIndex:this.index-1
      });
    }
  }
};
</script>

<style lang="less">
.song {
  text-align: left;
  
  display: flex;
  padding: 10px 0;
  padding-left: 5px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: rgb(252, 252, 252);
  position: relative;
  span {
    display: inline-block;
    font-size: 16px;
    
    
    
    color: #999;
    vertical-align: middle;
  }
  .rank{
    position: absolute;
    left:0;
    top:0;
    height: 100%;
    width:30px;
    display: flex;
    justify-content: center;
    align-items: center;
     span.index{
   
  }
  }
 
  .play {
    position: absolute;
    right:0;
    top:0;
    height: 100%;
    width:60px;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      // margin-top: 5px;
      width: 26px;
      height: 26px;
      line-height: 22px;
      margin:0 auto;
      border-radius: 50%;
      border: 1px solid #999;
      i {
        color: #999;
        font-size: 12px;
        font-weight: 300;
        margin-left: 8px;
      }
    }
  }
  .name {
    flex: 1;
    padding-right: 60px;
    padding-left: 30px;
    &.new-song{
      padding-left: 5px;
    }
    .author {
      font-size: 12px;
      color: #999;
      //  line-height: 18px;
      .sq {
        display: inline-block;
        width: 12px;
        height: 8px;
        background: url("../assets/musicicon.png") no-repeat;
        background-size: 166px 97px;
        vertical-align: middle;
        margin-right: 3px;
      }
    }
  }
}
</style>