<template>
  <div class="recommend">
    <Title>推荐歌单</Title>
    <ul class="recommend-list">
      <RecommendItem v-for="(item, index) in RecommendItems" :key="index" :item="item"></RecommendItem>
      <li class="roading-page" v-if="isRoading">
         <Roading></Roading>
      </li>
    </ul>
    <Title>最新音乐</Title>
    <ul class="recommend-music">
      <SongItem
        v-for="(item, index) in RecommendMusic"
        :key="index"
        :index="index+1"
        :songitem="item"
        :isNewSong="true"
        :playList="RecommendMusic"
        @translate-song-id="$emit('translate-song-id',$event)"
      ></SongItem>
    </ul>
    <p class="footer">已经没有更多啦~</p>
    
  </div>
</template>

<script>
import Title from "@/components/Title.vue";
import RecommendItem from "@/components/RecommendItem.vue";
import SongItem from "@/components/SongItem.vue";
import Roading from "@/components/Roading.vue";
export default {
  components: {
    Title,
    RecommendItem,
    SongItem,
    Roading
  },
  data() {
    return {
      RecommendItems: null,
      RecommendMusic: null,
      isRoading:false
    };
  },
  created() {
    this.getRecommendItem();
    this.getRecommendMusic();
  },

  methods: {
    getRecommendItem: function() {
      let item=JSON.parse(window.localStorage.getItem('RecommendItems'))
      // 当本地有数据且当前时间
      if(item&&Date.now()<item.time){
        this.RecommendItems=item.data
      }else{
        this.isRoading=true
         this.axios
        .get("https://music.kele8.cn/personalized?limit=6")
        .then(response => {
          // console.log(response.data.result);
          this.RecommendItems = response.data.result;
          window.localStorage.setItem(
            "RecommendItems",
            JSON.stringify({
              // 设置过期时间戳
              time: Date.now() + 10 * 60 * 1000,
              // 存储的数据
              data: response.data.result
            })
          );
        }).finally(() => {
          this.isRoading = false;
        });
      }
     
    },
    getRecommendMusic: function() {
      let newsong = JSON.parse(window.localStorage.getItem("RecommendMusic"));
      // 从本地拿数据 本地有数据 且时间还没过10分钟
      if (newsong && Date.now() < newsong.time) {
        this.RecommendMusic = newsong.data;
      } else {
        this.axios
          .get("https://music.kele8.cn/personalized/newsong")
          .then(response => {
            // console.log(response.data.result);
            this.RecommendMusic = response.data.result;
            window.localStorage.setItem(
              "RecommendMusic",
              JSON.stringify({
                // 设置过期时间戳
                time: Date.now() + 10 * 60 * 1000,
                // 存储的数据
                data: response.data.result
              })
            );
          });
      }
    }
  }
};
</script>

<style lang="less">
.recommend {
  ul.recommend-list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    li {
      flex: 1 1 30%;
      &:nth-child(3n + 2) {
        margin: 0 5px;
      }
      &.roading-page{
        background: #fff;
        width:100vw;
        position: absolute;
        top:0;
        height:20vh;
        .roading{
          position: absolute;
          top:50px;
          
        }
      }
    }
  }
  .footer {
    text-align: center;
    line-height: 2em;
    padding-bottom: 40px;
    color: #999;
    background: rgb(245, 245, 245);
  }
}
</style>