<template>
	<div
		class="music-controller"
		:class="{ 'is-play': !isPlay }"
		v-if="currentSong"
	>
		<div class="controller" @click="fullScreenPlayer()">
			<div class="pic">
				<img :src="currentSong.al.picUrl" alt="cover" />
			</div>
			<div class="name">
				<dl>
					<dt>{{ currentSong.name }}</dt>
					<dd>{{ currentSong.ar[0].name }}</dd>
				</dl>
			</div>

			<div class="play-pause" @click.stop="playOrpause">
				<canvas></canvas>
				<span>
					<i class="fa fa-pause" aria-hidden="true" v-if="isPlay"></i>
					<i
						class="fa fa-play"
						aria-hidden="true"
						v-else-if="!isPlay"
					></i>
				</span>
			</div>
		</div>
		<section class="palyer-full-sreen" v-if="isFullScreen">
			<div
				class="song-bg"
				:style="{ 'background-image': `url(${currentSong.al.picUrl})` }"
			></div>
			<div class="song-content">
				<div class="close-fullscreen" @click="isFullScreen = false">
					<i class="fa fa-compress" aria-hidden="true"></i>
				</div>
				<div class="disc">
					<div class="cover">
						<span class="pause" v-if="!isPlay" @click="playOrpause">
							<i class="fa fa-play" aria-hidden="true"></i>
						</span>
						<img
							:src="currentSong.al.picUrl"
							alt
							@click="playOrpause"
						/>
					</div>
				</div>
				<div class="song-name">
					<dl>
						<dt>{{ currentSong.name }}</dt>
						<dd></dd>
					</dl>
				</div>
				<div class="lyric-box">
					<ul>
						<li
							:class="{ active: item.isActive }"
							v-for="(item, index) in lyricArr"
							:key="index"
						>
							{{ item.text }}
						</li>
					</ul>
				</div>
				<div class="full-screen-player">
					<div class="progress-bar">
						<span class="ct">{{ ct }}</span>
						<div class="progress">
							<span
								class="progress-bg"
								:style="{
									width: (progress / 1000) * 100 + '%'
								}"
							></span>
							<input
								type="range"
								@input="progressControl($event.target.value)"
								v-model="progress"
								value="0"
								min="0"
								max="1000"
							/>
						</div>
						<span class="dt">{{ dt }}</span>
					</div>

					<div class="controler">
						<span class="prev" @click="prevSong">
							<i class="fa fa-backward" aria-hidden="true"></i>
						</span>

						<span class="pause-play" @click="playOrpause">
							<i
								class="fa fa-pause"
								aria-hidden="true"
								v-if="isPlay"
							></i>
							<i
								class="fa fa-play"
								aria-hidden="true"
								v-else-if="!isPlay"
							></i>
						</span>
						<span class="next" @click="nextSong">
							<i class="fa fa-forward" aria-hidden="true"></i>
						</span>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
export default {
	props: {
		songId: Number,
		playList: Array,
		currentIndex: Number
	},

	computed: {
		translateid: function() {
			return this.songId;
		},
		current: {
			get() {
				return this.currentIndex;
			},
			set(newValue) {
				this.$emit("currentIndex", newValue);
			}
		},
		palylist() {
			return this.playList;
		},
		dt() {
			let m = parseInt(this.durationTime / 1000 / 60);
			let s = parseInt((this.durationTime / 1000) % 60);
			let minute = m >= 10 ? m : "0" + m;
			let second = s >= 10 ? s : "0" + s;
			return minute + ":" + second;
		}
	},
	data() {
		return {
			src: null,
			isPlay: false,
			currentSong: null,
			audio: null,
			isFullScreen: false,
			Intervalid: null,
			currentTime: null,
			durationTime: null,
			progress: null,
			ct: null,
			lyric: [],
			lyricArr: []
		};
	},
	watch: {
		songId: function(newValue) {
			this.getCurrentSongInfo(newValue);
			this.src = `https://music.163.com/song/media/outer/url?id=${newValue}.mp3`;
		},
		src: function(newValue) {
			// this.isPlay = false;
			this.src = newValue;
			this.audio.src = newValue;
			this.isPlay = true;
		},
		currentSong(newValue) {
			this.$nextTick(() => {
				this.durationTime = newValue.dt;
				window.clearInterval(this.Intervalid);
				this.drawCircleProgress();
			});
		},
		currentTime(newValue) {
			this.ctime(newValue);
			this.progress = (this.currentTime / this.durationTime) * 1000;
		},
		// isPlay(newValue) {
		// 	this.$nextTick(() => {
		// 		if (newValue) {
		// 			this.drawCircleProgress();
		// 		} else {
		// 			window.clearInterval(this.Intervalid);
		// 		}
		// 	});
		// }
	},

	mounted() {
		this.init();
	},

	methods: {
		init: function() {
			this.audio = document.createElement("audio");
			this.audio.autoplay = "autoplay";
			this.audio.src = this.src;
			// window.console.log(this.audio);
		},
		// 画圆形歌曲进度条
		drawCircleProgress() {
			let canvas = this.$el.querySelector("canvas");
			canvas.width = 36;
			canvas.height = 36;
			let ctx = canvas.getContext("2d");
			// 定时器
			this.Intervalid = window.setInterval(() => {
				this.currentTime = this.audio.currentTime * 1000;
				// 如果全屏播放 每秒更新当前播放时间
				if (this.isFullScreen) {
					let cts = this.$el.querySelector(".progress-bar .ct");
					cts.innerHTML = this.ct;
					var lyricBox = this.$el.querySelector(".lyric-box ul");
					this.lyricScroll(this.currentTime);
				}
				// 监测音乐是否播放完毕
				if (this.audio.ended) {
					this.isPlay = false;
					// 音乐播放完成自动播放下一曲
					this.nextSong();
				}

				// window.console.log("currentTime");
				// 每次先清空画布
				ctx.clearRect(0, 0, 36, 36);
				// 画圆
				ctx.beginPath();
				ctx.lineWidth = 2;
				ctx.strokeStyle = "rgba(0, 0, 0,0.2)";
				ctx.arc(18, 18, 17, 0, 2 * Math.PI);
				ctx.closePath();
				ctx.stroke();
				// 画进度条
				ctx.beginPath();
				ctx.lineWidth = 2;
				ctx.strokeStyle = "rgb(255, 77, 77)";
				ctx.arc(
					18,
					18,
					17,
					-0.5 * Math.PI,
					(2 * Math.PI * this.currentTime) / this.currentSong.dt -
						0.5 * Math.PI
				);
				ctx.stroke();
			}, 100);
		},
		// 获取歌曲信息
		getCurrentSongInfo: function(id) {
			this.getLyric(id);
			this.axios
				.get(`https://music.kele8.cn/song/detail?ids=${id}`)
				.then(response => {
					// window.console.log(response);
					this.currentSong = response.data.songs[0];
				});
		},
		// 获取歌词
		getLyric(id) {
			this.axios
				.get(`https://music.kele8.cn/lyric?id=${id}`)
				.then(response => {
					this.lyric = response.data.lrc.lyric.split("\n");
					this.parseLyric(this.lyric);
				});
		},

		// 解析歌词
		parseLyric(arr) {
			arr.forEach((element, index) => {
				if (index != arr.length - 1) {
					var n = element.split(/\]/gi);
					var m = n[n.length - 1];
					let t = element.slice(1, 9);
					this.lyricArr[index] = {
						time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
						text: m.trim() != "" ? m.trim() : "------------",
						isActive: false
					};

					// window.console.log(n,m)
				}
			});
		},

		// 歌词滚动
		lyricScroll(time) {
			let index = 0;
			// 如果当前时间小于或等于某段歌词的时间 就让歌词高亮
			for (let ind = 0; ind < this.lyricArr.length; ind++) {
				if (this.lyricArr[ind].time * 1000 >= time) {
					index = ind;
					break;
				}
			}
			this.lyricArr.forEach((e, i) => {
				if (i == index) {
					if (index < 1) {
						this.lyricArr[0].isActive = true;
					} else {
						this.lyricArr[i - 1].isActive = true;
						if (this.isFullScreen) {
							let lyricBox = this.$el.querySelector(
								".lyric-box ul"
							);
							lyricBox.style.marginTop = -i * 29 +100+ "px";
							// window.console.log(lyricBox.scrollTop)
						}
					}
				} else {
					this.lyricArr[i].isActive = false;
				}
			});
		},

		// 播放暂停
		playOrpause: function() {
			if (this.isPlay) {
				this.audio.pause();
				this.isPlay = false;
			} else {
				this.audio.play();
				this.isPlay = true;
			}
		},
		// 上一曲
		prevSong() {
			let index = this.current;
			index -= 1;
			this.current = this.current - 1;
			this.current =
				this.current < 2 ? this.playList.length - 1 : this.current - 1;
			this.getCurrentSongInfo(this.playList[index].id);
			this.src = `https://music.163.com/song/media/outer/url?id=${this.playList[index].id}.mp3`;
		},
		// 下一曲
		nextSong() {
			let index = this.current;
			index += 1;
			this.current =
				this.current > this.playList.length - 3 ? 0 : this.current + 1;
			window.console.log(index);
			this.getCurrentSongInfo(this.playList[index].id);
			this.src = `https://music.163.com/song/media/outer/url?id=${this.playList[index].id}.mp3`;
		},

		fullScreenPlayer() {
			this.isFullScreen = true;
		},
		progressControl(value) {
			this.audio.currentTime =
				((value / 1000) * this.durationTime) / 1000;
		},

		// 当前播放时间
		ctime(value) {
			let m = parseInt(value / 1000 / 60);
			let s = parseInt((value / 1000) % 60);
			let minute = m >= 10 ? m : "0" + m;
			let second = s >= 10 ? s : "0" + s;
			this.ct = minute + ":" + second;
		}
	}
};
</script>

<style lang="less">
.music-controller {
	background: rgba(255, 255, 255, 0.829);
	border-top: 1px solid rgb(235, 235, 235);
	&.is-play {
		.controller {
			.pic {
				animation-play-state: paused;
			}
		}
		.palyer-full-sreen {
			.song-content {
				.disc {
					.cover {
						img {
							animation-play-state: paused;
						}
					}
				}
			}
		}
	}
	.controller {
		width: 100vw;
		height: 46px;
		padding: 5px 10px;
		display: flex;
		.pic {
			width: 36px;
			height: 36px;
			border-radius: 50%;
			animation: cover-rotate 5s linear infinite;
			box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.13);
			img {
				width: 36px;
				height: 36px;
				border-radius: 50%;
			}
		}
		.name {
			flex: 1;
			text-align: left;
			margin-left: 15px;
			dl {
				dt {
				}
				dd {
					font-size: 12px;
				}
			}
		}

		.play-pause {
			width: 36px;
			height: 36px;
			// background: yellow;
			position: relative;
			span {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				// border-radius: 50%;
				// border:2px solid #999;
				i {
					height: 36px;
					display: flex;
					flex-direction: column;
					justify-content: center;
					color: rgba(136, 136, 136, 0.781);
				}
			}
		}
	}
	.palyer-full-sreen {
		height: 100vh;
		width: 100vw;
		position: relative;
		background: rgb(88, 88, 88);
		.song-bg {
			height: 100vh;
			width: 100vw;
			// transform: scale(1.5);
			filter: blur(10px);
			background-size: cover;
			background-position: center;

			&::before {
				content: "";
				display: block;
				width: 100%;
				height: 100%;
				background: rgba(12, 12, 12, 0.6);
				// filter: blur(60px)
			}
		}
		.song-content {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
			padding-top: 40px;
			.close-fullscreen {
				width: 70px;
				height: 80px;
				position: absolute;
				top: -35px;
				left: -30px;
				color: white;
				background: rgba(27, 27, 27, 0.349);
				font-size: 26px;
				transform: rotate(45deg);
				line-height: 80px;
				text-align: right;
				i {
					transform: rotate(-45deg);
					margin-right: 5px;
				}
			}
			.disc {
				width: 100vw;
				display: flex;
				justify-content: center;
				align-items: center;
				&::before {
					content: "";

					display: block;
					width: 100px;
					height: 150px;
					position: absolute;
					top: 0;
					left: 48%;
					z-index: 4;
					background-image: url("../assets/needle-ip6.png");
					background-position: top center;
					background-size: 80%;
					background-repeat: no-repeat;
				}
				.cover {
					width: 290px;
					height: 290px;
					background-image: url("../assets/disc.png");
					background-size: cover;
					display: flex;
					justify-content: center;
					align-items: center;
					position: relative;
					span.pause {
						width: 60px;
						height: 60px;
						position: absolute;
						top: 50%;
						left: 50%;
						margin-top: -30px;
						margin-left: -30px;
						color: white;
						border: 2px solid #fff;
						border-radius: 50%;
						z-index: 3;
						text-align: center;
						line-height: 60px;
						font-size: 24px;
						opacity: 0.8;
					}
					img {
						width: 180px;
						height: 180px;
						border-radius: 50%;
						animation: cover-rotate 10s linear infinite;
					}
				}
			}
			.song-name {
				color: #fff;

				dl {
					dt {
						line-height: 40px;
						font-size: 18px;
					}
					dd {
						font-size: 12px;
						color: rgb(212, 212, 212);
						// height:15px;
					}
				}
			}
			.lyric-box {
				height: 200px;
				overflow: hidden;
				color: rgb(187, 187, 187);
				margin-bottom: 10px;

				ul {
					margin-top: -90px;
					transition: all 0.3s;
					// overflow-y: auto;
					height: 200px;
					li {
						// height: 30px;
						line-height: 19px;
						padding: 5px;
						font-size: 14px;
						// transition: all 0.3s;
						&.active {
							color: white;
							// font-size: 16px;
						}
					}
				}
			}
			.full-screen-player {
				width: 100vw;
				color: #fff;
				// padding:0 10px;
				.progress-bar {
					width: 100%;
					display: flex;
					align-items: center;
					margin-bottom: 5px;
					.ct,
					.dt {
						width: 60px;
						line-height: 18px;
						font-size: 14px;
					}

					.progress {
						flex: 1;
						height: 3px;
						background: rgba(141, 141, 141, 0.473);
						position: relative;
						// overflow: hidden;
						border-radius: 10px;

						span.progress-bg {
							display: block;
							height: 100%;
							width: 10px;
							border-radius: 10px;
							background: rgb(189, 189, 189);
							position: relative;
							&::after {
								content: "";
								display: block;
								position: absolute;
								top: 50%;
								right: 0;
								margin-top: -5px;
								width: 10px;
								height: 10px;
								border-radius: 50%;
								background: rgb(255, 255, 255);
							}
						}
						input {
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 2px;
							opacity: 0;
						}
					}
				}
				.controler {
					width: 100%;
					display: flex;
					padding: 5px;
					justify-content: space-around;
					span {
						padding: 5px 10px;
					}
					.prev {
					}
					.pause-play {
						display: block;

						//  background: red;
					}
					.next {
					}
				}
			}
		}
	}
}
@keyframes cover-rotate {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
</style>
