<template>
  <div class="search">
    <div class="search-box">
      <div>
        <span>
          <i class="fa fa-search" aria-hidden="true"></i>
        </span>
        <input type="text" v-model="keyWords" placeholder="搜索歌曲、歌手、专辑" @focus="isSearchInput" @input="isSearchInput" @keyup.enter="getSearchResult(keyWords)" />
        <span class="clear" v-if="keyWords" @click="keyWords=null ;searchResult = null">
          <i class="fa fa-times-circle" aria-hidden="true"></i>
        </span>
      </div>
    </div>
    <section class="hot-suggets" v-if="!keyWords">
      <p class="title">热门搜索</p>
      <ul>
        <li v-for="(item, index) in hotSuggest" :key="index">
          <span @click="suggestSearch(item.first) ;getSearchResult(item.first)">{{item.first}}</span>
        </li>
      </ul>
    </section>
    <section class="search-list" v-else-if="keyWords&&!searchResult">
      <p>搜索："{{keyWords}}"</p>
      <ul>
        <li v-for="(item, index) in searchList" :key="index" @click="getSearchResult(item.keyword)">
          <i class="fa fa-search" aria-hidden="true"></i>
          {{item.keyword}}
        </li>
      </ul>
    </section>
    <section class="search-song-list" v-else-if="searchResult">
      <p class="tag">搜索结果</p>
      <ul>
        <SongItem
          v-for="(item, index) in searchResult"
          :key="index"
          :index="index+1"
          :songitem="item"
          :isSearchSong="true"
           :playList="searchResult"
          @translate-song-id="$emit('translate-song-id',$event)"
        ></SongItem>
      </ul>
    </section>
    <template v-if="isRoading">
      <div class="roading-page">
        <Roading></Roading>
      </div>
    </template>
  </div>
</template>

<script>
import SongItem from "@/components/SongItem.vue";
import Roading from "@/components/Roading.vue";
export default {
  components: {
    SongItem,
    Roading
  },
  data() {
    return {
      keyWords: null,
      hotSuggest: null,
      searchList: null,
      searchResult: null,
      isRoading: false
    };
  },
  watch: {
    keyWords: function(newValue) {
      
      this.searchList = null
      this.isRoading = true;
      if(!newValue){
        this.isRoading = false;
      }
      this.axios
        .get(
          `https://music.kele8.cn/search/suggest?keywords= ${newValue} &type=mobile`
        )
        .then(response => {
          //  window.console.log(response)
          this.searchList = response.data.result.allMatch;
        })
        .finally(() => {
          this.isRoading = false;
        });
    }
  },
  created() {
    this.getSuggest();
  },
  methods: {
    getSuggest: function() {
      this.isRoading = true
      this.axios.get(`https://music.kele8.cn/search/hot`).then(response => {
        // window.console.log(response);
        ({ hots: this.hotSuggest } = response.data.result);
      }).finally(() => {
          this.isRoading = false;
        });
    },
    suggestSearch: function(hot) {
      this.keyWords = hot;
    },
    getSearchResult: function(words) {
     this.isRoading= true
      this.axios
        .get(`https://music.kele8.cn/search?keywords=${words}`)
        .then(response => {
         
          this.searchResult = response.data.result.songs;
        }).finally(() => {
          this.isRoading = false;
        });
    },
    isSearchInput: function() {
      if (this.keyWords) {
        this.searchResult = null;
        
      }
    }
  }
};
</script>

