<template>
  <li class="recommend" @click="showDetail(item)">
    <img :src="item.picUrl" alt />
    <span class="play-count"><i class="fa fa-headphones" aria-hidden="true"></i>{{translatePlayNum(item.playCount)}}</span>
    <p class="name">{{item.name}}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: Object
  },
  methods: {
    translatePlayNum: function(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      }
      if (n > 99999) {
        return (n / 10000).toFixed(1) + "万";
      }
    },
    showDetail:function(item){
    
      this.$router.push({ path: 'songlistdetail', query: { id: item.id }})
    }
  }
};
</script>

<style lang="less">
li.recommend {
  position: relative;
  img {
  }
  span.play-count {
    position: absolute;
    top: 5px;
    right: 5px;
    color: white;
    font-size: 12px;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
    i{
      margin-right:3px;
    }
  }
  p.name {
    display: -webkit-box;
    font-size: 14px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin: 0 5px 10px 5px;
  }
}
</style>